@Component({
  selector: 'app-root',
  templateUrl: 'app.root.component.html',
  styleUrls: ['app.component.css','../../../bootstrap.css'],
  encapsulation: ViewEncapsulation.None
})
export class AppComponent {
  title = 'app works!';
}



